body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  padding: 0px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.report-area {
  margin-left: 60px;
}

.container-fluid {
  padding-right: 0;
  padding-left: 0;
}

.reports{
  margin-top:50px;
}
.main-header{
  height: 139px;
}

.toolnavigation {
	height: 50px;
	width: 100vw;
	border-bottom: 1px solid #b1b3b3;
	background-color:#002c6c;
	z-index: 10;
}

.header {
	border-bottom: 1px solid #b1b3b3;
	width: 100%;
	padding: 0;
  margin: 0;
  z-index: 10;
	height: 90px;
  background-color: #f4f4f4;
  z-index: 100;
}

.logo{
  margin-left: 20px; 
  width: 141px; 
  padding-right: 35px; 
  height: auto; 
  border-right: 2px solid #b1b3b3; 
  margin-top: 10px;
  margin-right: 10px;
}

.tooltitle-m {
	border-bottom: none !important;
	margin-left: 180px !important;
	font-size: 1.4em !important;
  padding: 15px 0 9px 0;
  margin-top: 10px;
  vertical-align: middle;
}



.main-menu-fixed {
  width: 60px;
  min-height: 100%;
  background-color: #002c6c;
  position: fixed;
}

.toolkit .bg-blue {
  background-color: #002c6c !important;
}

.ul-menu {
  list-style: none;
  padding: 0;
  font-size: 12px;
  margin-left: 14px;
}

.ul-menu > li > div:last-child {
  position: fixed;
  visibility: hidden;
  opacity: 0;
  top: 83px;
  left: 52px;
  min-height: 100%;
  width: 50px;
  background-color: #fff;
  -webkit-transition: 0.3s ease;
  transition: 0.3s ease;
  z-index: 9999;
}
.ul-menu > li:hover {
  background-color: #008dbd;
}
.ul-menu > li:hover > div:last-child {
  visibility: visible;
  opacity: 1;
  width: 250px;
  box-shadow: 2px 0 5px -2px #888;
}

.image-menu-svg-24,
.image-menu-home,
.image-menu-dash,
.image-menu-copy,
.image-menu-conf,
.image-menu-settings,
.image-menu-combo,
.image-menu-back {
  position: relative;
  top: 0;
  left: 13px;
  background-repeat: no-repeat;
  width: 24px;
  height: 24px;
}

.ul-menu {
  list-style: none;
  padding: 0;
  font-size: 12px;
}
.ul-menu > li {
  margin: 5px 0;
}

.toolbar {
  margin-left: 0px;
}

.grid {
  width: 95%;
}

.title {
  border-color: #ffffff;
}

